@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  height: 100vh;
  background: linear-gradient(0deg, rgb(6, 119, 209) 0%, rgb(1, 202, 122));
  text-align: center;
  color: white;
}

body *{
  font-family: roboto, sans-serif;
}

#newgame {
  background: transparent;
  color: white;
  font-size: 30px;
  border: 5px double white;
  padding: 10px 20px;
  margin-top: 40px;
}

#won_msg{
	font-size: 40px;
	color: white;
	padding: 30px;
}
#lost_msg{
	font-size: 40px;
	color: red;
	padding: 30px;
}

h1 {
  color: wheat;
  text-align: center;
  font-size: 60px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#keyboard {
    margin-top: 30px;
    max-width: 600px;
    display: inline-block;
}

#keyboard button {
    background: transparent;
    cursor: pointer;
    color: white;
    padding: 5px 20px;
    border: 0;
    margin: 5px;
    font-size: 18px;
    display: inline-block;
    width: 30px;
    text-align: center;
}

#keyboard button.used{
    opacity: 0.5;
}
#current_word span{
    font-size: 40px;
    border-bottom: 1px solid white;
    margin: 0.5px;
    padding: 5px 20px;
    color: white;
}

#current_word span.lost{
    color: red;
}

#current_word span.notfound{
    color: rgba(255, 255, 255, 0.3);
}
#life {
    margin-top: 35px;
}

#life .count {
    background-color: red;
    display: inline-block;
    height: 20px;
    margin: 0 10px;
    position: relative;
    top: 0;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    width: 20px;
  }

#life .count:before,
#life .count:after {
    content: "";
    background-color: red;
    border-radius: 50%;
    height: 20px;
    position: absolute;
    width: 20px;
  }
  
#life .count:before {
    top: -10px;
    left: 0;
  }
  
#life .count:after {
    left: 10px;
    top: 0;
  }

#life .count.empty {
    opacity: 0.4;
}
